// import Factory from './model/_factory'
import Ninja from './sprite/_ninja'
// import Bg from './sprite/_bg'
import Cloud from './sprite/_cloud'
import Mountain from './sprite/_mountain'
import Ground from './sprite/_ground'
import Jump from './sprite/_janpBtn'
import River from './sprite/_river'
import Maluta from './sprite/_malta'
import Rook from './sprite/_rook'
import Musha from './sprite/_musha'
import Ashigaru from './sprite/_ashigaru'
import Hinotama from './sprite/_hinotama'
import { MyLabel } from './model/_label'
import {enemysTimeline} from './config/enemy'
import {MySprite} from './model/_sprite'
import {random} from './functions'

const GAME_CLEAR = 'img/game_clear.png'
const GAME_OVER = 'img/gameover.png'
const PASSWORD_MAKIMONO = 'img/password_makimono.png'
const PASSWORD_INTRODUCTION = 'img/password_introduction2.png'
const TAP_HERE = 'img/taphere.png'

class Main {
  constructor() {
    console.log('Main.constructor')
    this.game = null
    this.SCROLL_SPEED = -10
    this.SCROLL_FLG = 0
  }

  init(game) {
    console.log('Main.init')
    this.fps = 24
    this.game = game
    this.game.fps = this.fps
    // this.game.addEventListener('load', () => {
    //   this.game.pushScene( this.game.mainScene() )
    // })
    // this.game.mainScene = () => {
    //   const scene = new Scene()
    //   scene.backgroundColor = 'black'
    //   return scene
    // }
    this.game.onload = () => this.load()
    this.game.on('enterframe', () => this.on())
    this.items = {}
    this.asset = {}
    this.flameCount = 0
    this.second = 0
    this.scene = null
    this.gameStart = false
    this.restart = 0
  }

  hookAdd(name, hookClass) {
    this.enemysClass[name] = (applyGroup) => new hookClass(this).apply(applyGroup)
  }

  enemyApply(name, applyGroup) {
    const data = this.enemysClass[name]
    if (data) return data(applyGroup)
  }

  facade() {
    console.log('Main.facade')
    this.enemysClass = {}
    this.game.preload(
      'img/ninja.png',
      'img/btn_jump.png',
      'img/kawa.png',
      'img/cloud.png',
      'img/mountain.png',
      'img/grand.png',
      'img/wind.png',
      'img/rook.png',
      'img/musha.png',
      'img/ashigaru.png',
      'img/hinotama.png',
      'img/bakuhatsu3.png',
      'img/bakuhatsu1.png',
      'bgm/se_maoudamashii_retro08.mp3',
      'bgm/MusMus-BGM-122.mp3',
      'bgm/MusMus-BGM-115.mp3',
      'bgm/MusMus-BGM-104.mp3',
      'bgm/panti.mp3',
      'bgm/bakuhatu.mp3',
      'img/koka_ninja.png',
      GAME_OVER,
      GAME_CLEAR,
      PASSWORD_MAKIMONO,
      PASSWORD_INTRODUCTION,
      TAP_HERE,
    )

    this.hookAdd('Ashigaru', Ashigaru)
    this.hookAdd('Hinotama', Hinotama)
  }

  on() {
    console.log('Main.on')
    // 
    if (this.gameStart) {
      this.flameCount += 1
      if (this.isSecond()) this.second += 1

      // 
      if (this.isSecond() && this.SCROLL_FLG > 0) this.SCROLL_FLG -= 1
      // 
      if (this.isSecond() && this.SCROLL_FLG === 0) this.SCROLL_SPEED = -10

      if (this.isSecond()) {
        const applyArray = enemysTimeline
          .filter((item) => item.count === this.second)
          .filter((item) => item.mode <= this.mode)
        applyArray.map((item) => this.enemyApply(item.class ,this.charaGroup))
      }

      const isSecond = this.isSecond()
      if (isSecond && random(1, 3) === 1) new Cloud(this).apply(this.bgGroup)
      if (isSecond && random(1, 4) === 1) new Cloud(this).apply(this.bgGroup)
      if (isSecond && random(1, 5) === 1) new Mountain(this).apply(this.bgGroup)
      if (isSecond && random(1, 5) === 1) new Rook(this).apply(this.bgGroup)
      // if (isSecond && random(1, 9) === 1) new River(this),apply(this.bgGroup)
      // if (isSecond && num === 2) new Maluta(this).apply()
    }
  }

  isSecond() {
    // 
    return this.flameCount % this.fps === 0
  }

  timerInit() {
    this.flameCount = 0
    this.second = 0
  }

  removeScene(scene) {
    if (!scene) return
    while(scene.firstChild){
        scene.removeChild(scene.firstChild);
    }
  }

  replaceScene(scene) {
    this.game.replaceScene(scene)
    this.scene = scene
  }

  titleScene(old) {
    this.removeScene(old)
    if (this.bgm) this.bgm.stop()
    const scene = new Scene()
    scene.addChild(MyLabel(scene, 180, 200, '', 27, 'center', '#ffffff'))
    scene.addChild(MyLabel(scene, 180, 250, '', 27, 'center', '#ffffff'))

    const easy = MyLabel(scene, 180, 340, '', 27, 'center', '#ffffff')
    const nomal = MyLabel(scene, 180, 400, '', 27, 'center', '#ffffff')
    const hard = MyLabel(scene, 180, 460, '', 27, 'center', '#ffffff')
    scene.addChild(easy)
    scene.addChild(nomal)
    scene.addChild(hard)

    easy.on('touchstart', () => this.gameScene(scene, 1))
    nomal.on('touchstart', () => this.gameScene(scene, 2))
    hard.on('touchstart', () => this.gameScene(scene, 3))
    scene.backgroundColor = '#4a62e6'
    this.replaceScene(scene)

    const bgGroup = new Group()
    // new Cloud(this).init().apply(bgGroup)
    // new Cloud(this).init().apply(bgGroup)
    // new Mountain(this).init().apply(bgGroup)
    // this.bgGroup = bgGroup
    scene.addChild(bgGroup)
  }

  gameScene(old, mode = 2) {
    // 
    this.mode = mode

    this.removeScene(old)
    this.timerInit()
    const scene = new Scene()

    this.bgm = this.game.assets['bgm/MusMus-BGM-122.mp3'].clone()
    this.bgm.play()
    this.replaceScene(scene)
    scene.backgroundColor = '#4a62e6'

    const ctlGroup = new Group()
    const bgGroup = new Group()
    const charaGroup = new Group()

    this.items.prayer = new Ninja(this).apply(charaGroup)
    this.items.boss = new Musha(this).apply(charaGroup)
    this.items.jumpBtn = new Jump(this).apply(ctlGroup)

    new Ground(this).apply(bgGroup)
    new Cloud(this).init().apply(bgGroup)
    new Cloud(this).init().apply(bgGroup)
    new Mountain(this).init().apply(bgGroup)
    new Rook(this).apply(bgGroup)

    scene.addChild(bgGroup)
    scene.addChild(charaGroup)
    scene.addChild(ctlGroup)
    this.bgGroup = bgGroup
    this.charaGroup = charaGroup

    this.gameStart = true
  }

  overScene(old) {
    this.removeScene(old)
    const scene = new Scene()
    scene.backgroundColor = '#333'
    scene.addChild(MyLabel(scene, 170, 250, 'GAME OVER', 40, 'center', '#ffffff'))
    // scene.addChild(MyLabel(scene, 190, 300, ``, 30, 'center', '#ffffff'))
    // scene.addChild(MySprite(189, 97, this.game.assets[GAME_OVER], 240, 190, 2))
    scene.addChild(MyLabel(scene, 180, 400, ``, 30, 'center', '#ffffff'))

    scene.on('touchstart', () => this.titleScene())
    this.replaceScene(scene)
    if (this.bgm) this.bgm.stop()
    this.bgm = this.game.assets['bgm/MusMus-BGM-104.mp3'].clone()
    this.bgm.play()
  }

  clearScene(old) {
    this.removeScene(old)
    const scene = new Scene()
    scene.addChild(MyLabel(scene, 160, 280, 'GAME CLEAR', 30, 'center'))
    scene.addChild(MyLabel(scene, 190, 330, ` ${this.second}`, 30, 'center'))
    scene.addChild(MyLabel(scene, 160, 380, `${this.restart}`, 30, 'center'))
    scene.on('touchstart', () => this.gameclearScene())
    this.replaceScene(scene)
  }

  gameclearScene(old) {
    this.removeScene(old)
    const scene = new Scene()
    scene.backgroundColor = 'black'

    scene.addChild(MySprite(126, 24, this.game.assets[GAME_CLEAR], 265, 95, 2))
    scene.addChild(MySprite(100, 70, this.game.assets[PASSWORD_MAKIMONO], 270, 215, 2))
    scene.addChild(MySprite(65, 18, this.game.assets[PASSWORD_INTRODUCTION], 296, 332, 2))

    // //
    const taphere = new Sprite(127,25)
    taphere.image = this.game.assets[TAP_HERE]
    taphere.x = 260
    taphere.y = 490
    taphere.scale(2)
    const opa = 0.08
    taphere.opacity = opa
    var flg = 0
    taphere.onenterframe = function(){
        if(taphere.opacity < 1 && flg == 0){
            taphere.opacity += opa
        }else{
            if(taphere.opacity >= 1){
                flg = 1
            }
            if(taphere.opacity <= 0){
                flg = 0
            }
            var opa_check = taphere.opacity - opa
            if(opa_check < 0){
                taphere.opacity = 0
            }else{
                taphere.opacity -= opa
            }
        }
    }
    scene.addChild(taphere)
    // taphere.on('touchstart', () => this.endScene())
    taphere.on('touchstart', () =>window.location.href = 'https://genmakai.github.io/site/')


    const label = MyLabel(scene, 300, 224, '', 32, 'left')
    label.color = 'black'
    label.font = "64px 'Meiryo', '', ' Pro W3', sans-serif"
    scene.addChild(label)

    // scene.on('touchstart', () => this.endScene())
    this.replaceScene(scene)
  }

  endScene(old) {
    this.removeScene(old)
    const scene = new Scene()
    scene.addChild(MyLabel(scene, 280, 80, '', 26, 'left'))
    scene.addChild(MyLabel(scene, 220, 180, 'BGMMusMus <br>https://musmus.main.jp/<br>Mid-range Strength<br><br>', 16, 'left'))
    scene.on('touchstart', () => this.titleScene())
    this.replaceScene(scene)
  }

  intersectPrayer(chara) {
    const prayer = this.getData(this.items, 'prayer.item')
    if (!prayer) return false
    return prayer.intersect(chara)
  }

  start() {
    console.log('')
    this.titleScene()
    this.game.start()
  }

  stop() {
    console.log('')
    this.game.stop()
    this.gameStart = false
  }

  over() {
    this.overScene()
    this.restart += 1
    this.gameStart  = false
  }

  clear() {
    console.log('')
    // this.clearScene()
    this.gameclearScene()
    this.gameStart  = false
  }

  load() {
    console.log('')
  }

  scroll() {
    return this.SCROLL_SPEED
  }

  getData(object, path, defaultValue) {
    if (!object) return null
    const propertyArray = path.split('.')
    let result = object
    try {
      propertyArray.map((value) => {
        const r = result[value]
        result = r
      })
      return result
    } catch (e) {
      return defaultValue
    }
  }
}
export default new Main()
