/**
 * MySprite // 
 *
 * @param {number} w
 * @param {number} h
 * @param {object} asset
 * @param {number} x
 * @param {number} y
*/
export function MySprite(w, h, asset, x, y, scale = 1) {
  const item = new Sprite(w, h)
  item.image = asset
  item.x = x
  item.y = y
  item.scale(scale)
  return item
}
